import React, { useState } from "react";
import LocationAndCategory from "./tabs/LocationAndCategory";
import TopicContent from "./tabs/TopicContent";
import Media from "./tabs/Media";
import Preview from "./tabs/Preview";
import Header from "../../semantics/header";
import Footer from "../../semantics/footer";
import checkMark from "../../assets/images/check-mark.png";
import { arrayBufferToBlob } from "blob-util";

import "./style.scss";

class TopicSuggestion extends React.Component {
  state = {
    page: 1,

    // location and category
    categories: [],
    categoryId: null,
    lat: "",
    lng: "",
    address: "",
    // topic content
    topicContent: {
      title: "",
      description: "",
      amount: ""
    },
    // media
    imagesToPreview: [],
    imagesToUpload: []
  };

  componentDidMount() {
    this.getCategories();
  }

  getCategories = async () => {
    try {
      const response = await fetch("https://2.evollai.com/api/topic/category", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "Accept-Language": "*"
        }
      });
      const result = await response.json();
      if (result) {
        this.setState({
          categories: result.data
        });
      }
    } catch (error) {
      // console.log(error);
    }
  };

  topicContentFormChange = e => {
    const {
      topicContent: { form }
    } = this.state;

    form[e.target.name] = e.target.value;
    this.setState({ form });
  };

  moveToTopicContent = () => {
    const { page, categoryId, address } = this.state;

    this.setState({
      submittedOne: true
    });

    console.log(categoryId, this.state.address);

    if (page === 1 && categoryId && address) {
      this.setState({
        page: 2,
        checkMarkOne: true
      });
    }
  };

  moveToMedia = () => {
    const {
      page,
      topicContent: { title, description }
    } = this.state;

    this.setState({
      submittedTwo: true
    });

    if (page === 2 && title && description.length >= 500) {
      this.setState({
        page: 3,
        checkMarkOne: true,
        checkMarkTwo: true
      });
    }
  };

  moveToReview = () => {
    const { page, imagesToPreview } = this.state;
    this.setState({
      submittedThree: true
    });

    if (page === 3 && imagesToPreview.length >= 1) {
      this.setState({
        page: 4,
        checkMarkOne: true,
        checkMarkTwo: true,
        checkMarkThree: true
      });
    }
  };

  getFileBlob = file =>
    new Promise(res => {
      const fileReader = new FileReader();
      fileReader.onloadend = async e => {
        const arrayBuffer = e.target.result;
        const blob = await arrayBufferToBlob(arrayBuffer, file.type);
        res(blob);
      };
      fileReader.readAsArrayBuffer(file);
    });

  publishHandler = async e => {
    const { locationAndCategory, topicContent, media } = this.state;
    let token = localStorage.getItem("authToken");
    const formData = new FormData();
    const file = this.state.imagesToUpload[0].url;

    const blob = await this.getFileBlob(file);

    formData.append("address", this.state.address);
    formData.append("amount", topicContent.form.amount);
    formData.append("description", topicContent.form.description);
    formData.append("title", topicContent.form.title);
    formData.append("categoryId", this.state.categoryId);
    formData.append("lat", this.state.lat);
    formData.append("lng", this.state.lng);
    formData.append("file", blob);

    try {
      const res = await fetch("https://2.evollai.com/api/topic", {
        method: "POST",
        body: formData,
        headers: {
          "Content-Type": "application/json",
          "Accept-Language": "*",
          Authorization: `Bearer ${token}`
        }
      });
      const data = await res.json();
    } catch (error) {
      console.log(error);
    }
  };

  sendData = (imagesToPreview, imagesToUpload) => {
    this.setState({ imagesToPreview, imagesToUpload });
  };

  getLatLng = (lat, lng, address) => {
    this.setState({ lat, lng, address });
  };

  onCategoryChange = e => {
    this.setState({
      categoryId: e.target.value
    });
  };

  render() {
    const {
      checkMarkOne,
      checkMarkTwo,
      checkMarkThree,
      page,
      submittedOne,
      submittedTwo,
      submittedThree,
      submittedFour,
      address,
      topicContent: {
        form: { title, description, amount }
      }
    } = this.state;
    return (
      <>
        <Header />
        <div className="suggest-topic">
          <div className="tabs">
            <ul>
              <li className={checkMarkOne ? "done-tab" : ""}>
                Category and <br />
                <span>location</span>
                {checkMarkOne && <img src={checkMark} alt="check-mark" />}
              </li>
              <li className={checkMarkOne && checkMarkTwo ? "done-tab" : ""}>
                Topic content
                {checkMarkOne && checkMarkTwo && (
                  <img src={checkMark} alt="check-mark" />
                )}
              </li>
              <li
                className={
                  checkMarkOne && checkMarkTwo && checkMarkThree
                    ? "done-tab"
                    : ""
                }
              >
                Media
                {checkMarkOne && checkMarkTwo && checkMarkThree && (
                  <img src={checkMark} alt="check-mark" />
                )}
              </li>
              <li>Preview</li>
            </ul>
          </div>
          <div className="tab-content">
            {page === 1 ? (
              <LocationAndCategory
                moveNextPage={this.moveToTopicContent}
                onCategoryChange={this.onCategoryChange}
                address={address}
                categories={this.state.categories}
                submitted={submittedOne}
                categoryId={this.state.categoryId}
                getLatLng={this.getLatLng}
              />
            ) : page === 2 ? (
              <TopicContent
                onChange={this.topicContentFormChange}
                moveNextPage={this.moveToMedia}
                title={title}
                description={description}
                amount={amount}
                submitted={submittedTwo}
                typed={this.state.typed}
              />
            ) : page === 3 ? (
              <Media
                sendData={this.sendData}
                moveNextPage={this.moveToReview}
                submitted={submittedThree}
              />
            ) : page === 4 ? (
              <Preview
                images={this.state.imagesToPreview}
                publish={this.publishHandler}
                submitted={submittedFour}
              />
            ) : (
              <LocationAndCategory
                moveNextPage={this.moveToTopicContent}
                onCategoryChange={this.onCategoryChange}
                address={address}
                categories={this.state.categories}
                submitted={submittedOne}
                categoryId={this.state.categoryId}
              />
            )}
          </div>
        </div>
        <Footer />
      </>
    );
  }
}

export default TopicSuggestion;
